<template>
  <div ref="wordcloudChart" class="wordcloud-container"></div>
</template>

<script>
import * as echarts from 'echarts';
import 'echarts-wordcloud';

export default {
  name: 'WordCloud',
  mounted() {
    this.initChart();
  },
  beforeUnmount() {
    if (this.chart) {
      this.chart.dispose();
    }
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$refs.wordcloudChart);
      const colorList = [
        '#c23531', '#2f4554', '#61a0a8', '#d48265',
        '#91c7ae', '#749f83', '#ca8622', '#bda29a',
        '#6e7074', '#546570', '#c4ccd3', '#f05b72',
        '#ef5b9c', '#f47920', '#905a3d', '#fab27b',
        '#2a5caa', '#444693', '#726930', '#b2d235'
      ];
      const option = {
        tooltip: {
          show: true,
          trigger: 'item',
          formatter: function (params) {
            return `${params.data.description}`;
          },
          textStyle: {
            fontSize: 12
          },
          extraCssText: 'box-shadow: 0 0 5px rgba(0, 0, 0, 0.3); white-space: normal; width: 250px; padding: 8px; border-radius: 4px; background-color: #fff;',
          position: function (point, params, dom, rect, size) {
            const chartRect = this.$refs.wordcloudChart.getBoundingClientRect();
            const tooltipWidth = size.contentSize[0];
            const tooltipHeight = size.contentSize[1];
            let x = point[0];
            let y = point[1];

            // Á°Æ‰øù tooltip ‰∏çË∂ÖÂá∫Âè≥ËæπÁïå
            if (x + tooltipWidth > chartRect.width) {
              x = chartRect.width - tooltipWidth - 5;
            }
            // Á°Æ‰øù tooltip ‰∏çË∂ÖÂá∫‰∏ãËæπÁïå
            if (y + tooltipHeight > chartRect.height) {
              y = chartRect.height - tooltipHeight - 5;
            }
            // Á°Æ‰øù tooltip ‰∏çË∂ÖÂá∫Â∑¶ËæπÁïå
            if (x < 0) {
              x = 5;
            }
            // Á°Æ‰øù tooltip ‰∏çË∂ÖÂá∫‰∏äËæπÁïå
            if (y < 0) {
              y = 5;
            }

            return [x, y];
          }.bind(this)
        },
        series: [{
          type: 'wordCloud',
          data: [
            { name: 'ËÆ°ÁÆóÊú∫ÁßëÂ≠¶‰∏éÊäÄÊúØ', value: 1000, description: 'ËÆ°ÁÆóÊú∫ÁßëÂ≠¶‰∏éÊäÄÊúØÊòØ‰∏ÄÈó®ÊôÆÈÄöÈ´òÁ≠âÂ≠¶Ê†°Êú¨Áßë‰∏ì‰∏öÔºåÊòØËÆ°ÁÆóÊú∫Á±ª‰∏ì‰∏öÁöÑÂü∫Á°Ä‰∏ì‰∏ö‰πã‰∏Ä„ÄÇËØ•‰∏ì‰∏ö‰∏ªË¶ÅÁ†îÁ©∂ËÆ°ÁÆóÊú∫ÁöÑÁêÜËÆ∫„ÄÅÁÆóÊ≥ï„ÄÅÊï∞ÊçÆÁªìÊûÑ„ÄÅÊìç‰ΩúÁ≥ªÁªüÁ≠âÊñπÈù¢ÁöÑÁü•ËØÜÔºåÂüπÂÖªÂ≠¶ÁîüÂÖ∑Â§áÊâéÂÆûÁöÑËÆ°ÁÆóÊú∫Âü∫Á°ÄÁêÜËÆ∫ÂíåÂÆûË∑µËÉΩÂäõÔºåËÉΩÂ§ü‰ªé‰∫ãËÆ°ÁÆóÊú∫Á≥ªÁªüÁöÑËÆæËÆ°„ÄÅÂºÄÂèë„ÄÅÁª¥Êä§ÂíåÁÆ°ÁêÜÁ≠âÂ∑•‰Ωú„ÄÇ' },
            { name: 'ËΩØ‰ª∂Â∑•Á®ã', value: 600, description: 'ËΩØ‰ª∂Â∑•Á®ãÊòØ‰∏ÄÈó®Á†îÁ©∂Áî®Â∑•Á®ãÂåñÊñπÊ≥ïÊûÑÂª∫ÂíåÁª¥Êä§ÊúâÊïàÁöÑ„ÄÅÂÆûÁî®ÁöÑÂíåÈ´òË¥®ÈáèÁöÑËΩØ‰ª∂ÁöÑÂ≠¶Áßë„ÄÇËØ•‰∏ì‰∏öÊ≥®ÈáçÂüπÂÖªÂ≠¶ÁîüÁöÑËΩØ‰ª∂ÂºÄÂèëËÉΩÂäõÔºåÂåÖÊã¨ËΩØ‰ª∂ÈúÄÊ±ÇÂàÜÊûê„ÄÅËÆæËÆ°„ÄÅÁºñÁ†Å„ÄÅÊµãËØïÂíåÁª¥Êä§Á≠âÊñπÈù¢ÁöÑÊäÄËÉΩÔºå‰ΩøÂ≠¶ÁîüËÉΩÂ§üËøêÁî®Â∑•Á®ãÂåñÁöÑÊñπÊ≥ïÂíåÂ∑•ÂÖ∑ËøõË°åËΩØ‰ª∂È°πÁõÆÁöÑÂºÄÂèëÂíåÁÆ°ÁêÜ„ÄÇ' },
            { name: '‰∫∫Â∑•Êô∫ËÉΩ', value: 300, description: '‰∫∫Â∑•Êô∫ËÉΩÊòØÁ†îÁ©∂„ÄÅÂºÄÂèëÁî®‰∫éÊ®°Êãü„ÄÅÂª∂‰º∏ÂíåÊâ©Â±ï‰∫∫ÁöÑÊô∫ËÉΩÁöÑÁêÜËÆ∫„ÄÅÊñπÊ≥ï„ÄÅÊäÄÊúØÂèäÂ∫îÁî®Á≥ªÁªüÁöÑ‰∏ÄÈó®Êñ∞ÁöÑÊäÄÊúØÁßëÂ≠¶„ÄÇËØ•‰∏ì‰∏öËÅöÁÑ¶‰∫éÊú∫Âô®Â≠¶‰π†„ÄÅÊ∑±Â∫¶Â≠¶‰π†„ÄÅËá™ÁÑ∂ËØ≠Ë®ÄÂ§ÑÁêÜ„ÄÅËÆ°ÁÆóÊú∫ËßÜËßâÁ≠âÈ¢ÜÂüüÔºåÂüπÂÖªÂ≠¶ÁîüÊéåÊè°‰∫∫Â∑•Êô∫ËÉΩÁöÑÊ†∏ÂøÉÊäÄÊúØÂíåÊñπÊ≥ïÔºåËÉΩÂ§ü‰ªé‰∫ã‰∫∫Â∑•Êô∫ËÉΩÁ≥ªÁªüÁöÑËÆæËÆ°„ÄÅÂºÄÂèëÂíåÂ∫îÁî®Á≠âÂ∑•‰Ωú„ÄÇ' },
            { name: 'ÁîµÂ≠ê‰ø°ÊÅØÂ∑•Á®ã', value: 380, description: 'ÁîµÂ≠ê‰ø°ÊÅØÂ∑•Á®ã‰∏ì‰∏ö‰∏ªË¶ÅÁ†îÁ©∂ÁîµÂ≠êÁîµË∑Ø„ÄÅ‰ø°Âè∑Â§ÑÁêÜ„ÄÅÈÄö‰ø°ÂéüÁêÜ„ÄÅËÆ°ÁÆóÊú∫ÊäÄÊúØÁ≠âÊñπÈù¢ÁöÑÁü•ËØÜÔºåÂüπÂÖªÂ≠¶ÁîüÂÖ∑Â§áÁîµÂ≠ê‰ø°ÊÅØÁ≥ªÁªüÁöÑËÆæËÆ°„ÄÅÂºÄÂèë„ÄÅÊµãËØïÂíåÁª¥Êä§Á≠âËÉΩÂäõÔºåËÉΩÂ§üÂú®ÁîµÂ≠ê‰ø°ÊÅØÈ¢ÜÂüü‰ªé‰∫ãÁõ∏ÂÖ≥Â∑•‰Ωú„ÄÇ' },
            { name: 'ÁΩëÁªúÁ©∫Èó¥ÂÆâÂÖ®', value: 85, description: 'ÁΩëÁªúÁ©∫Èó¥ÂÆâÂÖ®‰∏ì‰∏öËá¥Âäõ‰∫éÂüπÂÖªËÉΩÂ§üÂ∫îÂØπÁΩëÁªúÂÆâÂÖ®ÊåëÊàòÁöÑ‰∏ì‰∏ö‰∫∫Êâç„ÄÇËØ•‰∏ì‰∏öÊ∂âÂèäÁΩëÁªúÂÆâÂÖ®ÊäÄÊúØ„ÄÅ‰ø°ÊÅØÂÆâÂÖ®ÁÆ°ÁêÜ„ÄÅÂØÜÁ†ÅÂ≠¶Á≠âÊñπÈù¢ÁöÑÁü•ËØÜÔºå‰ΩøÂ≠¶ÁîüÊéåÊè°ÁΩëÁªúÁ©∫Èó¥ÂÆâÂÖ®ÁöÑÊ†∏ÂøÉÊäÄÊúØÂíåÊñπÊ≥ïÔºåËÉΩÂ§üËøõË°åÁΩëÁªúÂÆâÂÖ®Èò≤Êä§„ÄÅÂÆâÂÖ®ËØÑ‰º∞ÂíåÂ∫îÊÄ•ÂìçÂ∫îÁ≠âÂ∑•‰Ωú„ÄÇ' },
            { name: 'Êô∫ËÉΩÁßëÂ≠¶‰∏éÊäÄÊúØ', value: 65, description: 'Êô∫ËÉΩÁßëÂ≠¶‰∏éÊäÄÊúØ‰∏ì‰∏öËûçÂêà‰∫ÜËÆ°ÁÆóÊú∫ÁßëÂ≠¶„ÄÅ‰∫∫Â∑•Êô∫ËÉΩ„ÄÅÊéßÂà∂ÁßëÂ≠¶Á≠âÂ§öÂ≠¶ÁßëÁü•ËØÜÔºåÊó®Âú®ÂüπÂÖªÂÖ∑Â§áÊô∫ËÉΩÁ≥ªÁªüËÆæËÆ°„ÄÅÂºÄÂèëÂíåÂ∫îÁî®ËÉΩÂäõÁöÑ‰∏ì‰∏ö‰∫∫Êâç„ÄÇËØ•‰∏ì‰∏öÊ≥®ÈáçÂüπÂÖªÂ≠¶ÁîüÂú®Êô∫ËÉΩÁÆóÊ≥ï„ÄÅÊú∫Âô®‰∫∫ÊäÄÊúØ„ÄÅÊô∫ËÉΩÊéßÂà∂Á≠âÊñπÈù¢ÁöÑÊäÄËÉΩ„ÄÇ' },
            { name: 'Áâ©ËÅîÁΩëÂ∑•Á®ã', value: 90, description: 'Áâ©ËÅîÁΩëÂ∑•Á®ã‰∏ì‰∏ö‰∏ªË¶ÅÁ†îÁ©∂Áâ©ËÅîÁΩëÁöÑÊÑüÁü•Â±Ç„ÄÅÁΩëÁªúÂ±ÇÂíåÂ∫îÁî®Â±ÇÁöÑÁõ∏ÂÖ≥ÊäÄÊúØÔºåÂåÖÊã¨‰º†ÊÑüÂô®ÊäÄÊúØ„ÄÅÊó†Á∫øÈÄö‰ø°ÊäÄÊúØ„ÄÅ‰∫ëËÆ°ÁÆó„ÄÅÂ§ßÊï∞ÊçÆÁ≠â„ÄÇÂüπÂÖªÂ≠¶ÁîüÂÖ∑Â§áÁâ©ËÅîÁΩëÁ≥ªÁªüÁöÑËÆæËÆ°„ÄÅÂºÄÂèëÂíåÁÆ°ÁêÜËÉΩÂäõÔºåËÉΩÂ§üÂú®Áâ©ËÅîÁΩëÈ¢ÜÂüü‰ªé‰∫ãÁõ∏ÂÖ≥Â∑•‰Ωú„ÄÇ' },
            { name: 'Êï∞Â≠óÂ™í‰ΩìÊäÄÊúØ', value: 60, description: 'Êï∞Â≠óÂ™í‰ΩìÊäÄÊúØ‰∏ì‰∏ö‰∏ªË¶ÅÊ∂âÂèäËÆ°ÁÆóÊú∫ÊäÄÊúØ„ÄÅÊï∞Â≠óÂõæÂÉèÂ§ÑÁêÜ„ÄÅÂä®ÁîªËÆæËÆ°„ÄÅÈü≥ËßÜÈ¢ëÂ§ÑÁêÜÁ≠âÊñπÈù¢ÁöÑÁü•ËØÜÔºåÂüπÂÖªÂ≠¶ÁîüÂÖ∑Â§áÊï∞Â≠óÂ™í‰ΩìÂÜÖÂÆπÁöÑÂàõ‰Ωú„ÄÅÂºÄÂèëÂíåÁÆ°ÁêÜËÉΩÂäõÔºåËÉΩÂ§üÂú®Êï∞Â≠óÂ™í‰ΩìÈ¢ÜÂüü‰ªé‰∫ãÁõ∏ÂÖ≥Â∑•‰Ωú„ÄÇ' }
          ],
          sizeRange: [16, 24],
          rotationRange: [0, 0],
          shape: 'circle',
          textStyle: {
            color: (params) => {
              return colorList[params.dataIndex % colorList.length];
            }
          },
          emphasis: {
            focus: 'self',
            textStyle: {
              transform: 'scale(1.2)',
              transformOrigin: 'center'
            }
          }
        }]
      };

      this.chart.setOption(option);
    }
  }
};
</script>

<style scoped>
.wordcloud-container {
  width: 100%;
  height: 100%;
}
</style>
  
  